@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Patua+One&display=swap');

/* — 기존 스타일 — */
.intro_wrap {
  width: 1400px;
  border-left: 3px solid #fa8e01;
  border-right: 3px solid #fa8e01;
  margin: 0 auto;
  position: relative;
}
.intro_wrap img {
  display: block;
  width: 400px;
  margin: 120px 0 0 0;
}
.intro_wrap .wartertext {
  position: absolute;
  right: 0;
  top: 0;
  text-align: left;
  /* 네 줄이 전부 보일 높이만큼 확보 */
  /* height: calc(4 * 8rem);  필요하면 풀어주세요 */
}

/* 공통 h2 스타일 */
.intro_wrap .wartertext h2 {
  font-family: "Patua One", serif;
  font-weight: 400;
  font-size: 8rem;
  line-height: 1;
  margin: 0;               /* 기본 여백 제거 */
  position: relative;      /* margin 조절만으로 움직이기 */
  pointer-events: none;
}

/* 스트로크 / 채움 분리 */
.intro_wrap .wartertext h2.stroke {
  color: transparent;
  -webkit-text-stroke: 2px #632600;
}
.intro_wrap .wartertext h2.fill {
  color: #632600;
  animation: water 6s ease-in-out infinite;
}

/* — 라인별 위치 조절 — */
/* 1번째: Web Designe 스트로크 */
.intro_wrap .wartertext h2.line1 {
  margin-top: 2rem;       /* ↑ 필요값으로 조절 */
  margin-left: 0;         /* ← 필요값으로 조절 */
}

/* 2번째: Web Designe 채움 */
.intro_wrap .wartertext h2.line2 {
  margin-top: -8rem;      /* 위의 line1과 겹치도록 */
  margin-left: 0;         /* ← 필요값으로 조절 */
}

/* 3번째: & Publisher 스트로크 */
.intro_wrap .wartertext h2.line3 {
  margin-top: 1rem;       /* 아래로 떨어뜨릴 거리 */
  margin-left: 0;         /* ← 필요값으로 조절 */
}

/* 4번째: & Publisher 채움 */
.intro_wrap .wartertext h2.line4 {
  margin-top: -8rem;      /* 위의 line3과 겹치도록 */
  margin-left: 0;         /* ← 필요값으로 조절 */
}

/* — 기존 물결 애니메이션 키프레임 — */
/* 물결 애니메이션 키프레임 (기존 clip-path) */
@keyframes water {
  0%, 100% {
    clip-path: polygon(
      100% 120%, 0% 120%, 0.00% 80.00%, 2.00% 79.65%, 4.00% 78.60%,
      6.00% 76.89%, 8.00% 74.58%, 10.00% 71.76%, 12.00% 68.52%, 14.00% 64.97%,
      16.00% 61.26%, 18.00% 57.49%, 20.00% 53.82%, 22.00% 50.36%, 24.00% 47.25%,
      26.00% 44.59%, 28.00% 42.47%, 30.00% 40.98%, 32.00% 40.16%, 34.00% 40.04%,
      36.00% 40.63%, 38.00% 41.90%, 40.00% 43.82%, 42.00% 46.31%, 44.00% 49.28%,
      46.00% 52.64%, 48.00% 56.25%, 50.00% 60.00%, 52.00% 63.75%, 54.00% 67.36%,
      56.00% 70.72%, 58.00% 73.69%, 60.00% 76.18%, 62.00% 78.10%, 64.00% 79.37%,
      66.00% 79.96%, 68.00% 79.84%, 70.00% 79.02%, 72.00% 77.53%, 74.00% 75.41%,
      76.00% 72.75%, 78.00% 69.64%, 80.00% 66.18%, 82.00% 62.51%, 84.00% 58.74%,
      86.00% 55.03%, 88.00% 51.48%, 90.00% 48.24%, 92.00% 45.42%, 94.00% 43.11%,
      96.00% 41.40%, 98.00% 40.35%, 100.00% 40.00%
    );
  }
  50% {
    clip-path: polygon(
      100% 120%, 0% 120%, 0% 20.00%, 2.00% 20.30%, 4.00% 21.40%, 6.00% 23.10%,
      8.00% 25.40%, 10.00% 28.20%, 12.00% 31.50%, 14.00% 35.00%, 16.00% 38.70%,
      18.00% 42.50%, 20.00% 46.20%, 22.00% 49.60%, 24.00% 52.80%, 26.00% 55.40%,
      28.00% 57.50%, 30.00% 59.00%, 32.00% 59.80%, 34.00% 60.00%, 36.00% 59.40%,
      38.00% 58.10%, 40.00% 56.20%, 42.00% 53.70%, 44.00% 50.70%, 46.00% 47.40%,
      48.00% 43.80%, 50.00% 40.00%, 52.00% 36.30%, 54.00% 32.60%, 56.00% 29.30%,
      58.00% 26.30%, 60.00% 23.80%, 62.00% 21.90%, 64.00% 20.60%, 66.00% 20.00%,
      68.00% 20.20%, 70.00% 21.00%, 72.00% 22.50%, 74.00% 24.60%, 76.00% 27.30%,
      78.00% 30.40%, 80.00% 33.80%, 82.00% 37.50%, 84.00% 41.30%, 86.00% 45.00%,
      88.00% 48.50%, 90.00% 51.80%, 92.00% 54.60%, 94.00% 56.90%, 96.00% 58.60%,
      98.00% 59.60%, 100.00% 60.00%
    );
  }
}



/* main.css에 추가 또는 profile.css로 분리 */
/* 전체 그리드 래퍼 */
.profile_grid_wrap {
  width: 1400px;
  margin: 0 auto;
  display: flex;
  padding: 60px 0 60px 0;
  min-height: 950px;
  font-family: 'Pretendard','Montserrat',sans-serif;
}

/* 왼쪽 카드 */
.profile_left {
  width: 340px;
  height: 514px;
  background: #fdeecb;
  border-radius: 180px 180px 180px 180px;
  padding: 40px 0 40px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 2px 20px rgba(0,0,0,0.03);
}
.profile_circle {
  position: relative;
  width: 220px;
  height: 220px;
  margin-bottom: 24px;
}
.profile_circle svg { display: block; }
.profile_circle .profile_img {
  position: absolute;
  left: 50%; top: 50%;
  transform: translate(-50%, -50%);
  width: 120px;
  height: 120px;
  border-radius: 50%;
  object-fit: cover;
  border: 7px solid #fff;
  box-shadow: 0 2px 8px rgba(0,0,0,0.10);
}
.profile_circle textPath {
  font-family: 'Pretendard','Montserrat',sans-serif;
  font-weight: 700;
  letter-spacing: 2px;
  fill: #232323;
}

.profile_info {
  margin-top: 24px;
  text-align: center;
}
.profile_info h2 {
  font-family: "Patua One", serif;
  font-weight: 700;
  font-size: 2rem;
  margin-bottom: 15px;
}
.profile_info p {
  font-size: 1.13rem;
  margin: 7px 0;
  color: #232323;
  font-family: 'Pretendard','Montserrat',sans-serif;
}

/* 오른쪽 (내용) */
.profile_right {
  flex: 1;
  padding-left: 70px;
  display: flex;
  flex-direction: column;
  gap: 38px;
}

.profile_right h2 {
  font-family: "Patua One", serif;
  font-weight: 700;
  font-size: 2.1rem;
  margin-bottom: 12px;
  letter-spacing: 0.01em;
}
.profile_edu, .profile_license, .profile_skill {
  margin-bottom: 6px;
}
.profile_edu div, .profile_license div {
  font-size: 1.09rem;
  color: #232323;
  font-weight: 400;
  line-height: 1.7;
  letter-spacing: 0.01em;
}
.profile_edu b, .profile_license b { font-weight: 700; }
.profile_edu i, .profile_license i { font-style: italic; }

/* skill */
.profile_skill {
  margin-top: 8px;
}
.profile_skill_row {
  display: flex;
  gap: 16px;
  margin-bottom: 14px;
  position: relative;
  align-items: center;
  /* background: #222; */
  border-radius: 14px;
  padding: 12px 20px 12px 18px;
  min-height: 56px;
  max-width: 710px;
}
.profile_skill_icon img {
  width: 48px;
  height: 48px;
  border-radius: 10px;
  background: #fff;
  transition: transform 0.18s;
  box-shadow: 0 2px 8px rgba(0,0,0,0.07);
}
.profile_skill_icon:hover img {
  transform: scale(1.10);
  z-index: 3;
}

/* 스킬 툴팁 (한 줄 설명) */
.profile_skill_tooltip {
  position: absolute;
  left: 0;
  bottom: -48px;
  width: 100%;
  background: #fff;
  color: #222;
  padding: 14px 22px 10px 14px;
  font-size: 1.07rem;
  border-radius: 0 0 12px 12px;
  box-shadow: 0 6px 24px rgba(0,0,0,0.16);
  z-index: 12;
  animation: fadeIn .2s;
  font-family: 'Pretendard','Montserrat',sans-serif;
  pointer-events: none;
}
.profile_skill_tooltip b {
  font-size: 1.12rem;
  font-weight: 700;
  margin-bottom: 0.6em;
  display: block;
  letter-spacing: 0.01em;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(8px);}
  to   { opacity: 1; transform: translateY(0);}
}


/* project파일 css */
.project_outer_wrap {
  width: 100%;
  background: none;
  min-height: 900px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 0;
}
.project_inner_wrap {
  width: 1400px;
  min-height: 900px;
  display: flex;
  flex-direction: row;
  gap: 0;
}
.project_wheel_area {
  flex: none;
  width: 600px;
  min-height: 800px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  position: relative;
  z-index: 2;
}
.circle_rotate_bg {
  position: absolute;
  right: 376px;
  top: -42px;
  width: 920px;
  height: auto;
  z-index: 0;
}
.circle_rotate_bg img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  pointer-events: none;
}
.project_buttons {
  position: absolute;
  top: 325px;
  left: 164px;
  display: flex;
  flex-direction: column;
  gap: 30px;
  z-index: 3;
}
.project_menu_btn {
  background: black;
  color: white;
  border: none;
  padding: 6px 12px;
  border-radius: 20px;
  cursor: pointer;
  font-size: 1.05rem;
  font-weight: 500;
}
.project_menu_btn.active {
  background: crimson;
}

.project_wheel_pin {
  position: absolute;
  right: 625px;
  top: 196px;
  width: 460px;
  height: auto;
  z-index: 5;
  transition: transform 0.2s ease;
}
.project_wheel_pin.bounce {
  animation: bouncePin 0.4s ease;
}
@keyframes bouncePin {
  0% { transform: translateY(0); }
  30% { transform: translateY(10px); }
  100% { transform: translateY(0); }
}

/* 오른쪽 콘텐츠 영역 */
.project_content_area {
  flex: 1;
  min-height: 700px;
  display: flex;
  align-items: center;
  padding-left: 60px;
  justify-content: flex-start;
}

/* 프로젝트 콘텐츠 */
.project_content_box {
  width: 700px;
  min-height: 390px;
  display: flex;
  flex-direction: column;
  gap: 26px;
}
.project_flex_row {
  display: flex;
  flex-direction: row;
  gap: 32px;
  align-items: flex-start;
  margin-bottom: 8px;
}
.project_img_card {
  background: #444;
  border: 3px solid #2189ff;
  border-radius: 9px;
  width: 350px;
  height: 280px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  overflow: hidden;
  box-shadow: 0 3px 18px rgba(0,0,0,0.10);
}
.project_img_label {
  position: absolute;
  top: 8px; left: 12px;
  background: rgba(255,255,255,0.89);
  color: #181818;
  border-radius: 5px;
  padding: 3px 16px;
  font-size: 1.05rem;
  font-family: 'Pretendard','Montserrat',sans-serif;
  font-weight: 600;
  z-index: 10;
}
.project_img_card img {
  width: 100%; height: 100%; object-fit: cover;
  display: block;
}
.project_img_desc {
  margin-top: 18px;
  background: #fff;
  border-radius: 8px;
  padding: 14px 18px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.06);
  font-size: 1.16rem;
  color: #232323;
}

/* contact칸 */
.contact_wrap {
  width: 100%;
  min-height: 800px;
  padding: 90px 0 90px 0;
  background: #fff9f3;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.contact_card {
  width: 470px;
  background: #fff;
  border-radius: 30px;
  box-shadow: 0 10px 38px rgba(212, 153, 109, 0.11), 0 1.5px 8px rgba(0,0,0,0.07);
  padding: 48px 40px 32px 40px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact_illust {
  width: 60px;
  margin-bottom: 18px;
}

.contact_card h2 {
  font-size: 2.5rem;
  font-family: 'Patua One', serif;
  color: #b45b35;
  margin-bottom: 8px;
}

.contact_intro {
  font-size: 1.17rem;
  color: #603813;
  line-height: 1.65;
  margin-bottom: 26px;
  font-family: 'Pretendard', 'Montserrat', sans-serif;
}

.contact_links {
  margin-bottom: 25px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.contact_email {
  color: #a45e2e;
  font-weight: bold;
  font-size: 1.05rem;
  background: #ffedd7;
  padding: 7px 20px;
  border-radius: 17px;
  text-decoration: none;
  transition: background 0.2s;
}
.contact_email:hover {
  background: #fae3c7;
}
.notion_btn {
  color: #fff;
  background: #ff9d3c;
  padding: 7px 23px;
  border-radius: 17px;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.03rem;
  letter-spacing: 1.3px;
  transition: background 0.2s;
}
.notion_btn:hover {
  background: #d7811b;
}

.contact_form {
  margin: 18px 0 8px 0;
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: 100%;
}
.contact_form input,
.contact_form textarea {
  width: 100%;
  padding: 13px;
  border: 1.5px solid #ffe7c9;
  border-radius: 12px;
  font-size: 1.07rem;
  font-family: 'Pretendard', sans-serif;
  background: #fffefa;
  outline: none;
  resize: none;
  transition: border 0.22s;
}
.contact_form input:focus,
.contact_form textarea:focus {
  border: 1.5px solid #ff9d3c;
}

.contact_form button {
  padding: 12px 0;
  background: #ff9d3c;
  color: #fff;
  border: none;
  border-radius: 13px;
  font-size: 1.14rem;
  font-family: 'Pretendard', sans-serif;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.19s;
}
.contact_form button:disabled {
  background: #bcbcbc;
  color: #fff;
  cursor: default;
}

.contact_footer_text {
  margin-top: 17px;
  color: #9c684c;
  font-size: 1.05rem;
  font-family: 'Pretendard', sans-serif;
}