@charset "utf-8";


:root{--m_color:#333;}
*{margin: 0px; padding: 0px;}
ul, ol, li{list-style: none;}
body{font-family: '맑은 고딕', arial,sans-serif;font-size: 12px;color: var(--m_color);}
a{text-decoration: none;color: var(--m_color);}
header{
  position: fixed;
  left: 0px; 
  top: 0px;
  width: 100%;
  height: 100px;
  z-index: 100;
  /* background: rgb(255, 255, 255); */
  color: #fff;
  /* box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); */
}
header > h1{float: left;padding: 30px 0px 0px 30px;}
header > h1 >img{width: 100px;margin-top: -10px;}
header nav{width: 40%;float: right;}
header nav > ul{display: flex;justify-content: space-around;}
header nav > ul >li{ line-height: 100px;}
header nav button{border: none;background: none;color: #632600;cursor: pointer;font-size: 1.3rem;}
header nav button:hover{text-decoration: underline;font-weight: bold;}

/* 메인서식 */
main{padding-top: 100px;}

/* 콘텐츠 서식 */
section{width: 100%;height: 100vh;transition: 0.5s;/* height: 100vh; = 사용자가 보는 화면을 무조건 꽉 채움 */}

/* 푸터서식 */
footer{background: #000;}
footer address{line-height: 40px;text-align: center;color: #fff;font-style: normal;}